const pipeline = (x) => x();

const importers = [
  async (path) => {
    if (!/^(?:\.\.\/\.\.\/packages\/ui\/(?!\.)[^/]*?(?:(?!(?:node_modules))[^/]*?)\/(?!\.)(?=.)[^/]*?\.stories\.(js|jsx|ts|tsx))$/.exec(path)) {
      return;
    }
  
    const pathRemainder = path.substring(18);
    return import(
      /* webpackChunkName: "[request]" */
      /* webpackInclude: /(?:\/packages\/ui\/(?!\.)[^/]*?(?:(?!(?:node_modules))[^/]*?)\/(?!\.)(?=.)[^/]*?\.stories\.(js|jsx|ts|tsx))$/ */
      '../../packages/ui/' + pathRemainder
    );
  }
  ,
  async (path) => {
    if (!/^(?:\.\.\/admin\/src\/components\/(?!\.)[^/]*?(?:(?!(?:node_modules))[^/]*?)\/(?!\.)(?=.)[^/]*?\.stories\.(js|jsx|ts|tsx))$/.exec(path)) {
      return;
    }
  
    const pathRemainder = path.substring(24);
    return import(
      /* webpackChunkName: "[request]" */
      /* webpackInclude: /(?:\/admin\/src\/components\/(?!\.)[^/]*?(?:(?!(?:node_modules))[^/]*?)\/(?!\.)(?=.)[^/]*?\.stories\.(js|jsx|ts|tsx))$/ */
      '../admin/src/components/' + pathRemainder
    );
  }
  
];

export async function importFn(path) {
  for (let i = 0; i < importers.length; i++) {
    const moduleExports = await pipeline(() => importers[i](path));
    if (moduleExports) {
      return moduleExports;
    }
  }
}